header{
height:100vh;
padding-top:4rem;
overflow: hidden;
}

.header__container{
    text-align: left;
    height: 100%;
    position: relative;
}
.shift{
    margin-left: 2rem;
}
.pos 
{
 position: absolute;
   top:20px;
   right:-90px;
}

.cta{
    margin-top:2.5 rem;
    flex-direction: flex;
    gap: 1.2rem;
    justify-content: center;
}

.header__socials::before {
    content: "";
    width: 1px;
    height: 4rem;
    background:var(--color-primary);
}

.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: fixed;
    left: 2rem;
    bottom: 12rem;
}

.header__socials::after {
    content: "";
    width: 1px;
    height: 4rem;
    background:var(--color-primary);
}
.header__socialss{
    display: flex;
    flex-direction: column;
    writing-mode: vertical-rl;
    align-items: center;
    gap: 0.8rem;
    position: fixed;
    right: 2rem;
    bottom: 10rem;
}

.header__socialss::after {
    content: "";
    width: 1px;
    height: 5rem;
    background:var(--color-primary);
}
.me 
{
    background: linear-gradient(var(--color-primary),transparent);
    width : 22rem;
    height: 30rem;
    position: absolute;
    left:calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

.scroll__down
{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

@media screen and (max-width: 1024px)
{
    .header{
     height: 68vh;
    }
    }
    



@media screen and (max-width: 600px)
{
    .header{
     height: 100vh;
    }

    .header__socials,
    .scroll__down{
        display: none;
    }
    .header__socialss
    {
        display: none;
    }   
}